@import url(https://fonts.googleapis.com/css?family=Oswald:500|Martel);
#homepage-vue-ap {
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
body {
  text-rendering: optimizeLegibility;
}
p {
  font-family: "Martel"
}
.entry-title, .page-title {
  font-family: 'Oswald';
  text-transform: uppercase;
  font-weight: normal;
}
.mh-excerpt-more {
  color: #BE1E2D
}
.mh-excerpt-more:hover {
  text-decoration: underline;
}
@media (min-width: 770px){
.home-grid {
    display: grid;
    grid-gap: 2%;
    grid-template-columns: repeat(2, 48%);
}
}

.featured-post {
  margin-bottom: 1rem;
}
.featured-post figure {
  height: 0;
  width: 100%;
  overflow: hidden;
  padding-bottom: 50%;
  position: relative;
  background: #222;
}
.featured-post figure img {
  width: 100%;
  -webkit-transform-origin: center;
          transform-origin: center;
  -webkit-transition: all 0.25s ease;
  transition: all 0.25s ease;
  position: absolute;
  top: 50%; left: 0;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.featured-post figure:hover img {
  -webkit-transform: scale(1.125) translateY(-45%);
          transform: scale(1.125) translateY(-45%);
  opacity: 0.8;
}
.featured-post figure figcaption {
  position: absolute;
  color: white;
  bottom: 0px;
  width: 100%;
  padding: 1.05rem;
  text-shadow: 0 0.125em 0.5em rgba(0,0,0.65);
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0,0,0,0)),to(rgba(0,0,0,0.85)));
  background: linear-gradient(to bottom, rgba(0,0,0,0) 0%,rgba(0,0,0,0.85) 100%);
}
.featured-post h1 {
  font-family: 'Oswald';
  text-transform: uppercase;
  font-size: 2rem;
  line-height: 1em;
  margin-bottom: 0.25em;
  font-weight: normal;
  letter-spacing: -0.025em;
}
.featured-post figure figcaption h1 a {
  color: white;
}
.featured-post-excerpt {
  background: #F7F7F7;
  padding: 1rem;
}
@media (min-width: 770px){
.featured-post-excerpt {
    padding: 1.05rem;
}
.featured-post-excerpt p {
    font-size: 1.05rem;
}
.featured-post h1 {
    font-size: 3rem;
}
}


















.action-button {
  border: 0;
  cursor: pointer;
  font: inherit;
  line-height: normal;
  overflow: visible;
  padding: 0;
  -webkit-user-select: none; /* for button */
     -moz-user-select: none;
      -ms-user-select: none;
  display: block;
  width: 100%;
  /* height: 3rem; */
  /* font-size: 1rem; */
  /* background: #DDD; */
  border: 2px solid #2a2a2a;
  font-weight: bold;
  text-align: center;
  color: #2a2a2a;
  background: transparent;
  text-transform: uppercase;
  padding: 10px 15px;
  font-size: 0.875rem;
}
.action-button:hover {
  border-color: #BE1E2D;
  color: #BE1E2D;
}
.action-button:focus {
  outline: none;
}

